$primary-color: white;
$secondary-color: black;
$font: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial, sans-serif;
$breakpoint-mobile: 680px;

.App {
  margin: 0;
  padding: 0;
  display: block;
}
.nav {
  display: flex;
  background-color: $secondary-color;
  width: 100%;
  height: 50px;
  @media (max-width: $breakpoint-mobile) {
    height: 600px;
    display: block;
    .nav-left .links a,
    .nav-right .links a {
      display: block;
      padding-bottom: 3px;
      width: 100%;
      font-size: 30px;
      font-weight: 300;
      text-align: center;
      color: $primary-color;
      background-color: $secondary-color;
      cursor: pointer;
      margin: 25px;
      line-height: 50px;
    }
  }
}

.nav-left {
  background-color: $secondary-color;
  color: $primary-color;
  font-size: 1rem;
  flex: 50%;
  display: flex;
  margin-left: 60px;
  align-items: center;
  justify-content: left;
  font-family: $font;
}
.nav-right {
  background-color: $secondary-color;
  color: $primary-color;
  font-size: 1rem;
  font-family: $font;

  flex: 50%;
  padding-right: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.nav-right .links a,
.nav-left .links a {
  color: $primary-color;
  margin: 0 15px;
  font-family: $font;
  height: 35px;

  text-decoration: none;

  &:hover {
    border-radius: 25px;
    background-color: rgb(48, 46, 46);
    width: 150px;
  }
}
.uber {
  font-family: $font;

  background-color: $secondary-color;
  border: none;
  text-align: center;
  align-items: center;
  font-size: 1.5rem;
  height: 35px;
  width: 150px;
  color: $primary-color;
}

.sign {
  font-family: $font;
  padding: 5px;
  border-radius: 25px;
  background-color: $primary-color;
  text-align: center;
  font-size: 1rem;
  height: 35px;
  width: 80px;
  color: $secondary-color;
  &:hover {
    background-color: rgb(238, 232, 232);
  }
}

.info {
  display: flex;
  width: 100%;
  height: 600px;
  @media (max-width: $breakpoint-mobile) {
    height: 900px;
    display: block;
    .info-left {
      height: 300px;
      margin-top: 10px;
    }
    .info-right {
      justify-content: center;
      align-items: center;
      margin-left: 20%;
      height: 300px;
    }
  }
}

.info-left {
  flex: 50%;
  align-items: center;
  justify-content: center;
  display: block;
  background-color: $primary-color;

  .name {
    width: 400px;
    height: 400px;
    margin-top: 40%;
    margin-left: 20%;
    display: block;
    text-align: flex-start;
    align-items: center;
    justify-content: center;
  }
  .po {
    width: 400px;
    color: $secondary-color;
    text-align: justify;
    font-size: 44px;
    line-height: 52px;
    font-weight: 500;
    font-family: UberMove, UberMoveText, system-ui, "Helvetica Neue", Helvetica,
      Arial, sans-serif;
  }
  .cow {
    text-align: justify;
    font-size: 16px;
    line-height: 24px;
    font-weight: normal;
    color: $secondary-color;
  }
}

.info-right {
  flex: 50%;
  margin-right: 130px;
}
.option {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 12px;
  float: left;
  margin: 0 25px 0 25px;
  padding: 14px 0 14px 0;
  .group-l,
  .group-r {
    width: 150px;
    height: 50px;

    line-height: 20px;
    font-size: 18px;
    color: $primary-color;
    background-color: $secondary-color;
    border: none;
    border-radius: 2px;
    text-align: center;
  }
  .group-r {
    margin-left: 20px;
    color: $secondary-color;
    background-color: #eeeeee;
  }
  .group-l:hover {
    background-color: rgb(37, 36, 36);
  }
}

.Base {
  margin: 320px;
  width: 93%;
  height: 500px;
  display: flex;
  background-color: $secondary-color;
  color: $primary-color;
  align-items: center;
  justify-content: center;
  margin-left: 40px;
  & {
    @media (max-width: $breakpoint-mobile) {
      display: block;
      height: 900px;
      .Base-L,
      .Base-R {
        width: 650px;
        height: 200px;
        align-items: center;
        justify-content: flex-start;
        position: absolute;
        margin-top: 120px;
      }
      .Base-L {
        height: 200px;
        width: 500px;
      }
      .Base-R {
        margin: 480px 20px;
        height: 500px;
      }
    }
    .Base-L {
      display: block;
      flex: 46.5%;
      align-items: center;
      justify-content: left;
      text-align: justify;

      .base-H1 {
        text-align: justify;
        justify-content: center;
        width: 90%;
        margin: 20px 70px;
        line-height: 52px;
        font-size: 44px;
        font-weight: 500;
        font-family: $font;
        color: $primary-color;
      }
      .base-H2 {
        width: 80%;
        @extend .base-H1;
        font-size: 18px;
        line-height: 24px;
        font-weight: normal;
        color: $primary-color;
        justify-content: left;
      }
    }
  }
  .Base-R img {
    align-items: center;
    width: 85%;
    margin-right: 30px;
  }
}
@media (max-width: $breakpoint-mobile) {
  .Dp,
  .Imgs {
    height: 300px;
    width: 500px;
    align-items: left;
    text-align: flex-start;
  }
  .Imgs {
    width: 500px;
    height: 350px;
  }
}

.sure {
  display: grid;
  grid-column-gap: 30px;
  grid-template-columns: 50% 50%;
  width: 1200px;
  height: 900px;
  margin: 30px;
  .sure-r,
  .sure-l {
    flex: 50%;
    display: block;
    margin: 20px 90px;
    padding: 20px;
  }

  .sure-img {
    width: 560px;
    height: 340px;
  }
  .sure-r-data h2,
  .sure-l-data h2 {
    font-size: 20px;
    font-weight: 480;
    line-height: 30px;
    font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial,
      sans-serif;
  }
  .sure-r-data p,
  .sure-l-data p {
    font-size: 16px;
    font-weight: normal;
    line-height: 24px;
    font-family: UberMoveText, system-ui, "Helvetica Neue", Helvetica, Arial,
      sans-serif;
  }
  .lop {
    text-decoration: none;
  }
  .Learn {
    color: #000000;
    margin-top: 40px;

    font-size: 18px;
    font-weight: normal;
    line-height: 24px;
    font-family: $font;
    background-size: 0% 6px, 100% 6px;
    padding-bottom: 6px;
    background-image: linear-gradient(transparent calc(100% - 1px), #000000 1px),
      linear-gradient(transparent calc(100% - 1px), #cbcbcb 1px);
    transition: all 500ms cubic-bezier(0.23, 1, 0.32, 1);

    background-repeat: no-repeat;
    cursor: pointer;
    background-position: 0 bottom, 0 bottom;
  }
}

@media (max-width: $breakpoint-mobile) {
  .sure {
    width: 650px;
    display: block;

    img {
      width: 300px;
    }
  }
}
.wrap {
  @extend .sure;
  display: block;
  margin-left: 6%;
  margin-top: -290px;

  width: 1260px;
  background-color: rgb(238, 232, 232);
  height: 1200px;
  align-items: flex-start;
  .sure-r-data {
    margin: 0 40px;
  }
}
.Erl {
  @media (max-width: $breakpoint-mobile) {
    margin-top: 200px;
  }
}
.wrappers {
  background-color: $secondary-color;
  color: $primary-color;
  display: block;
  width: 100%;

  .Line {
    color: $primary-color;
    .sodi {
      margin-left: 100px;
    }
  }
  section p {
    display: block;

    width: 300px;
    color: $primary-color;

    margin-left: 80%;
    margin-top: 50px;

    a {
      cursor: pointer;
      color: $primary-color;
      text-align: flex-end;
      list-style: none;
      text-decoration: none;
    }
    a:hover {
      color: lightgray;
    }
  }
  .home-link {
    display: flex;
    list-style-type: none;
    text-decoration: none;
    cursor: pointer;
    color: $primary-color;
    font-size: 1.5rem;
    margin-left: 100px;
    font-family: $font;
  }
  .c {
    width: 300px;
    display: grid;
    grid-template-columns: 80% 80% 80%;
    grid-template-rows: 3fr;
    grid-gap: 20px;
  }
  .c-1,
  .c-2,
  .c-3 {
    color: $primary-color;
    display: block;

    margin-left: 100px;
  }
  .c-1 a,
  .c-2 a,
  .c-3 a {
    align-items: flex-start;
    margin: 10px;
    display: block;
    list-style-type: none;
    text-decoration: none;
    cursor: pointer;
    color: white;
  }
  .c-1 a:hover,
  .c-2 a:hover,
  .c-3 a:hover {
    color: lightgrey;
  }
}

.last {
  width: 100%;
  align-items: flex-start;
  justify-content: space-around;
  display: flex;
  flex-direction: row;
  a {
    list-style-type: none;
    text-decoration: none;
    cursor: pointer;
    color: $primary-color;
  }
  a:hover {
    color: lightgray;
  }
}
.lane {
  display: block;

  margin: 30px 100px;
  padding: 20px 30px;
}
.symbols {
  display: flex;
  flex-direction: flex;
  align-items: space-evenly;
}
.appes {
  border-radius: 15px;
  border-color: white;
  color: white;
  display: flex;
  height: 50px;
  width: 60px;
}
.Content {
  display: block;
}
